import { Grid, GridItem } from "@chakra-ui/react";
import React from "react";
import Sidebar from "@/common/Sidebar";
import useColorModeStyles from "@/utils/useColorModeStyles";
import MainProfile from "@/components/profile/MainProfile";
import Header from "@/common/Header";
import { GetServerSideProps } from "next";

interface HomePageProps {
  user: string;
}

const HomePage: React.FC<HomePageProps> = ({ user }) => {
  const { bg, textColor, navBgColor } = useColorModeStyles();

  return (
    <Grid
      templateAreas={{
        base: `"header"
               "main"`,
        md: `"header header"
             "nav main"`,
      }}
      gridTemplateRows={{ base: "auto 1fr", md: "50px 1fr" }}
      gridTemplateColumns={{ base: "1fr", md: "50px 1fr" }}
      height="100vh"
      gap="0.5"
      color={textColor}
      fontWeight="bold"
    >
      {/* Header Section */}
      <GridItem area="header" bg={bg} p={4}>
        <Header />
      </GridItem>

      {/* Sidebar Section */}
      <GridItem
        area="nav"
        bg={navBgColor}
        display={{ base: "none", md: "block" }}
      >
        <Sidebar />
      </GridItem>

      {/* Main Content Section */}
      <GridItem area="main" pl="2" bg={navBgColor}>
        {/* Pass the user query parameter to MainProfile as a prop */}
        <MainProfile username={user} />
      </GridItem>
    </Grid>
  );
};

// Fetching query parameter via getServerSideProps
export const getServerSideProps: GetServerSideProps = async ({ query }) => {
  const { user } = query;

  // Ensure that the user query parameter exists
  if (!user || typeof user !== "string") {
    return {
      notFound: true,
    };
  }

  return {
    props: {
      user, // Pass the user as a prop to the page
    },
  };
};

export default HomePage;
